<template>
<div id="header">
<div class="h_img">
  <img src="../../../static/img/logo.jpg" alt="">
</div>
<div class="h_content">
  <p>您所在城市：{{city}}</p>
  <p>天气：{{weather}}&nbsp;实时气温：{{temperature}}&deg;C</p>

</div>
<div class="h_right">
  <router-link to="/home">分类</router-link>
  <router-link to="/login">登录</router-link>
  <router-link to="/search">搜索</router-link>
</div>
</div>
</template>

<script>
    export default {
        name: "Header",
      data(){
        return{
          list:[],
          //{longitude: 40.03023, latitude: 116.310665}
          longitude:"",//经度
          latitude:"",//纬度
          city:"",//当前所在城市
          weather:"",//天气
          cod:"",//编码
          region:"",//动态地区
          temperature:""//温度
        }
      },
      created(){
        var obj=JSON.parse(window.localStorage.getItem("showPosition"))
        console.log(obj)

        /*
        * 当从浏览器缓存中获取到obj那么让他执行获取当前地理位置，否则的话他就是默认的初始位置
        * */
        if(obj==null){
          //默认初始位置
          this.longitude=40.03023//经度
          this.latitude=116.310665//纬度

          this.$http.get(`https://restapi.amap.com/v3/geocode/regeo?output=JSON&location=${this.latitude},${this.longitude }&key=5a172710e7b7d1a0130535bee5377aa9&radius=1000&extensions=all`).then((res)=>{
            // console.log(res)
            // console.log(res.data.regeocode.addressComponent.province+res.data.regeocode.addressComponent.district)/所在城市
            this.city=res.data.regeocode.addressComponent.province+res.data.regeocode.addressComponent.district//所在城市
            // console.log(res.data.regeocode.addressComponent.adcode)//地理编码
            this.cod=res.data.regeocode.addressComponent.adcode
            //天气查询
            this.$http.get(`https://restapi.amap.com/v3/weather/weatherInfo?city=${this.cod}&key=5a172710e7b7d1a0130535bee5377aa9`)
              .then((res)=>{
                // console.log(res.data.lives[0].weather,res.data.lives[0].temperature)
                this.weather=res.data.lives[0].weather;//天气状况
                this.temperature=res.data.lives[0].temperature;//实时气温
              })
          })
          return
        }else {
          //动态获取到的位置
          this.longitude=obj.longitude//经度
          this.latitude=obj.latitude//纬度
          this.$http.get(`https://restapi.amap.com/v3/geocode/regeo?output=JSON&location=${this.latitude},${this.longitude }&key=5a172710e7b7d1a0130535bee5377aa9&radius=1000&extensions=all`).then((res)=>{
            // console.log(res)
            this.city=res.data.regeocode.addressComponent.province+res.data.regeocode.addressComponent.district//所在城市
            this.cod=res.data.regeocode.addressComponent.adcode
            //动态天气
            this.$http.get(`https://restapi.amap.com/v3/weather/weatherInfo?city=${this.cod}&key=5a172710e7b7d1a0130535bee5377aa9`)
              .then((res)=>{
                console.log(res)
                this.weather=res.data.lives[0].weather;//天气状况
                this.temperature=res.data.lives[0].temperature;//实时气温
              })
          })

        }

      },

    }
</script>

<style scoped>
#header{
  width: 100%;
  height: 1.71875rem;
  line-height: 1.71875rem;
  display: flex;
  border-bottom:1px dashed #C5C5C5;
}
#header .h_img{
	margin-bottom: 10px;
	width: 9.8125rem;
	height: 1.78125rem;
	flex: 3;
}
#header .h_content{
	flex: 5;
  line-height: 0.5rem;
  padding-top: 20px;
}
#header .h_right{
	flex: 4;
  display: flex;
  line-height: 1.71875rem;

}
#header .h_right a{
  flex: 1;
  font-size: 15px;
  font-weight: bolder;
  text-align: center;
  text-decoration: none;
  color: #636363;
}
#header .h_right a.router-link-exact-active.router-link-active {
	color: #FF7200;
	border-bottom: 2px solid #FF7200;
  margin-bottom: 2px;
}
#header .h_img img{
	width: 100%;
	height: auto;
}
</style>



