<template>
<div class="warp">
  <Header></Header>
  <div class="main">
    <div class="search">
      <input type="text" placeholder="请输入要搜索的内容" ref="TextValue" @input="getTextValue" class="s_sousou">
    </div>
    <p v-for="val,index in list"><span @click="getPValue" :id=val.id>{{val.name}}</span><span :v-show="IsShow">{{val.district}}&nbsp;&nbsp;&nbsp;&nbsp;</span></p>
  </div>
</div>
</template>

<script>
    import Header from "../public/Header";
    export default {
      name: "Search",
      components: {Header},
      data(){
        return{
          list:[],
          TextName:[],
          IsShow:false
        }
      },
      created(){
        this.username=this.$router.history.current.query.name
        console.log(this.$router)
        var url="../../../static/js/list.json"
        this.$http(url).then((res)=>{
          console.log(res.data)
          this.TextName=res.data.type[0].datainfo
        })
      },
      methods:{
        getTextValue:function () {
          // type=050301&location=116.481488,39.990464&city=北京&datatype=all
          //经度116.300145，纬度40.035394
          console.log(this.$refs.TextValue.value)
          this.$http.get("http://restapi.amap.com/v3/assistant/inputtips",{params:{
              key:"5a172710e7b7d1a0130535bee5377aa9",
              keywords:this.$refs.TextValue.value,
              city:"北京",
              location:116.300145+","+40.035394,
              datatype:"all"
            }}).then((res)=>{
              console.log(res.data.tips)
            this.list=res.data.tips
          })
        },
        getPValue:function () {
          var Text=event.target.innerText//获取p标签的值
          var TextiId=event.target.id//获取p标签中属性为id的值
          var arr=this.TextName//获取p标签中属性为id的值
          console.log(arr)
         for(var i=0;i<arr.length;i++){
            if(arr[i].name==Text){
              this.$router.push({path:"/detailedlist",query: { name:Text}})
              break
            }else {
              this.$router.push({path:"/slist" ,query: { id: TextiId}})
            }
          }
        }
      }
    }
</script>

<style scoped>
  .search{
    width: 100%;
    line-height: 1.5rem;
    background: #F6F6F6;
    border-bottom: 1px solid #D5D5D5;
  }
  .search .s_sousou{
    width: 90%;
    height: 1rem;
    border: 1px solid #B9B9B9;
    border-radius: 17.5px;
    padding-left: 10px;
    outline: none;
    box-shadow: 0 0 1px 1px rgba(0,0,0,0.1) inset;
  }
  .main p {
    display: flex;
    width: 100%;
    line-height: 1rem;
    text-align: center;
    font-size: 12px;
    font-weight: bolder;
    background: #cccccc;
    border-bottom: 1px solid #fff;
    color: #fff;
  }
  .main p span:first-child{
    padding-left: 10px;
    width: 100%;
    flex: 8;
    text-align: left;
    line-height: 1rem;

  }
  .main p span:last-child{
    text-align: right;
    width: 100%;
    line-height: 1rem;
    flex: 3;
  }
</style>



