<template>
    <div class="warp">
      <Header></Header>
      <div class="main">
        <div class="search">
          <input type="text" placeholder="请输入要搜索的内容" class="s_sousou">
        </div>
        <div class="minmain">
          <div class="m_left">
            <ul>
              <li v-for="val,index in listname" @click="btn(index)">{{val.tit}}</li>
            </ul>

          </div>
          <div class="m_right">
            <ul>
              <li v-for="val,index in list" @click="btn(index)">
               <div class="r_img"><img :src="val.img" alt=""></div>
                <div class="r_title"><router-link :to="{ path: '/detailedlist', query: { name: val.name}}">{{val.name}}</router-link></div>
               <div class="r_tit">{{val.datatype}}</div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
</template>

<script>

    import Header from "../public/Header";
    import getLocation from "../public/getLocation";
    import axios from 'axios'
    var that=this
    export default {
      name: "Home",
      components: {Header,getLocation},
      data(){
          return{
            listname:[],
            list:[],
            val:"常用",
            username:""
          }
      },
      //当进入组件之前，执行 beforRouteEnter 路由钩子函数
//       beforeRouteEnter(to,from,next){
// /*     to: Route: 即将要进入的目标 路由对象
//        from: Route: 当前导航正要离开的路由
//        next: Function: 一定要调用该方法来 resolve 这个钩子。执行效果依赖 next 方法的调用参数。
//        next(): 进行管道中的下一个钩子。如果全部钩子执行完了，则导航的状态就是 confirmed (确认的)。
//        next(false): 中断当前的导航。如果浏览器的 URL 改变了 (可能是用户手动或者浏览器后退按钮)，那么 URL 地址会重置到         from 路由对应的地址。
//        next('/') 或者 next({ path: '/' }): 跳转到一个不同的地址。当前的导航被中断，然后进行一个新的导航。你可以向   next 传递任意位置对象，且允许设置诸如    replace: true、name: 'home' 之类的选项以及任何用在 router-link 的 to prop 或  router.push 中的选项。
//  next(error): (2.4.0+) 如果传入 next 的参数是一个 Error 实例，则导航会被终止且该错误会被传递给 router.onError() 注册过的回调。
// */
//         console.log('当进入组件之前')
//         console.log(name)
//         var user=to.query.name
//         console.log(user==undefined)
//         if(user==undefined){
//           alert("亲，请您登录后查看")
//           next({path:"/login"})
//           return
//         }else {
//           next()
//           return
//         }
//       },
      created(){
        this.username=this.$router.history.current.query.name
          console.log(this.$router)
          var url="../../../static/js/list.json"
          this.$http(url).then((res)=>{
            // console.log(res.data.type)
            this.listname=res.data.type
          })
      },
      methods:{
        btn(index){
          this.list=this.listname[index].datainfo
        }
      }

    }
</script>

<style scoped>
.search{
	width: 100%;
	line-height: 1rem;
	background: #F6F6F6;
   border-bottom: 1px solid #D5D5D5;
}
.search .s_sousou{
	width: 90%;
	height: 0.6rem;
	border: 1px solid #B9B9B9;
	border-radius: 17.5px;
	padding-left: 10px;
	outline: none;
  box-shadow: 0 0 1px 1px rgba(0,0,0,0.1) inset;
}
.minmain{
flex: 1;
display: flex;
 width: 100%;
  height:14rem;
  background: #F6F6F6;
}
.minmain .m_left{
  width: 100%;
  height:100%;
  flex: 4;
  background: #FFFFFF;


}
.minmain .m_left ul{
  width: 100%;
  height:100%;
   border-right:1px solid #636363 ;
   background: #fff;
}
.minmain .m_left li{
  width: 100%;
  line-height:1.35rem;
  border-bottom:1px solid #636363 ;
  font-size: 22px;
}
.minmain .m_right{
  flex: 8;
  width: 100%;
  height:14rem;
  overflow-y: scroll;
}
.minmain .m_right ul{
  width: 100%;
  height:14rem;
  border-right:1px solid #636363 ;
}
.minmain .m_right li{
  width: 100%;
  line-height:2.45rem;
  display: flex;
  border-bottom:1px solid #636363 ;
  font-size: 15px;
}
.minmain .m_right li .r_img{
  margin-top: 0.1rem;
  margin-left: 0.1rem;
  flex: 1;
  width: 30%;
  height: 1.75rem;

}
.minmain .m_right li .r_img img{
	width: 100%;
	height: auto;
	margin: 0 auto;
}
.minmain .m_right li .r_title{
	flex: 1;
}
.minmain .m_right li .r_tit{
	flex: 1;
}
</style>



